// This file is shared across the demos.
import Link from 'next/link';
import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

 class FerryMenu extends React.Component {
  render() {
    return  (
      <List>
        {console.log(props)}
        {props.allRoutes.map(route => (
        <Link
          prefetch
          as={`post?id=${route.routeName}`}
          href={{ pathname: '/post', query: { id: route.routeName } }}
        >
         <ListItem button key={route.id}>
          <ListItemText primary={route.routeName} />
          </ListItem>
        </Link>
    ))}
      </List>
    );
  }


} 

FerryMenu.getInitialProps = async function() {
  const URL = 'https://ferrytrackerserver.now.sh/graphql';
  const query = `{
    allRoutes {
      id
      routeName
    }
  }`;
  const res = await request(URL, query);
  console.log(`All routes fetched. Count: ${res.allRoutes.length}`);
  return res;
};

export default FerryMenu;